import "./styles.css";
import React, { useState } from "react";
// import styled from "styled-components";

export default function App() {
  return <MyTabsComponent />;
}

const MyTabsComponent = () => {
  // Use console.log() for debugging
  const [selectedTab, setSelectedTab] = useState("Tab1");

  return (
    <div className="tabs">
      <Buttons setSelectedTab={setSelectedTab} selectedTab={selectedTab} />
      <View selectedTab={selectedTab} />
    </div>
  );
};

// export default MyTabsComponent;

const Buttons = (props) => {
  let { setSelectedTab, selectedTab } = props;
  const tabArr = ["Tab1", "Tab2", "Tab3"];
  return tabArr.map((x) => (
    <button
      title={x}
      disabled={x === selectedTab ? true : false}
      id={x}
      // style={{
      //   pointerEvents: x === selectedTab ? "none" : "visible",
      //   cursor: "pointer",
      //   border: x === selectedTab ? "1px solid grey" : "1px solid black",
      //   marginLeft: "10px",
      //   color: x === selectedTab ? "grey" : "black"
      // }}
      className={`${x === selectedTab ? "btn-active" : " btn"}`}
      onClick={() => {
        setSelectedTab(x);
      }}
    >
      {x}
    </button>
  ));
};

const View = (props) => {
  let { selectedTab } = props;
  return (
    <div style={{ marginLeft: "10px", marginTop: "10px" }}>
      Content of {selectedTab}
    </div>
  );
};
